import {UI, SVG} from "ui/UI";
import {geoPath, geoOrthographic} from "d3-geo/index";
import {geoVoronoi} from "d3-geo-voronoi/index";


export function getPoints(nrPoints=42) {
    let increm = [];
    for (let i = 0; i < nrPoints; i++) {
        increm.push([(Math.PI * i) % 1, (Math.E * i) % 1]);
        increm[i][0] = 2.0 * (increm[i][0] - 0.5);
        increm[i][1] = (2.0 * (increm[i][1] - 0.5)) / 2.0;
    }

    function randomPoint(time, speedU, speedV) {
        var u = Math.abs(time * speedU) % 360;
        var v = Math.abs(time * speedV / 180) % 1;
        return [u, 180 * Math.asin(2.0 * v - 1.0)]
    }

    const timeSeed = Date.now() / 333;
    return increm.map(deltas => randomPoint(timeSeed, deltas[0], deltas[1]))
}

const d3geovoro = geoVoronoi();

export function getVoronoi(points) {
    return d3geovoro(points).polygons();
}

